









import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class header extends Vue {
  //   @Prop() private msg!: string;
  private name: string = "";
  private pageName: string[] = [
    "HOME",
    "",
    "YoUX",
    ""
  ];
  mounted(): void {
    this.nameChange();
  }

  computed() {}

  private nameChange() {
    return this.$store.getters.getPageId;
  }
}
